import React from "react";
import {BurgerIcon, ListIcon, Logo, ProfileIcon} from "@ya.praktikum/react-developer-burger-ui-components";
import {Link} from "../link/link";
import styles from './app-header.module.css'
import {useHistory} from "react-router";

export const AppHeader = () => {
    const history = useHistory();
    return (
        <header>
            <nav className={styles.app_header}>
                <div className={styles.app_header_link_container}>
                    <Link icon={<BurgerIcon type="secondary"/>} text={''} onClick={()=>{history.replace('/ingredients')}}/>
                    <Link icon={<ListIcon type="secondary"/>} text={' '} onClick={()=>{history.replace('/feed')}}/>
                    <div className={styles.logo} onClick={()=>{history.replace('/ingredients')}}><Logo/></div>
                    <Link icon={<ProfileIcon type="secondary"/>} text={' '} onClick={()=>{history.replace('/profile')}}/>
                </div>
            </nav>
        </header>
    );
};